// about page component
import { useEffect, useState } from "react";
import H3 from "../../components/utils/H3";
import PageTitle from "../../components/utils/PageTitle";
import { about, name, reviews, skills } from "../../data/data";
import Skill from "./Skill";
import Review from "./Review";
import * as ROUTES from "../../routes/routes";
import { Link } from "react-router-dom";

export default function About() {
	const [reviewCount, setReviewCount] = useState(4);

	// update the document title on page load and make sure the window scroll position is at the top
	useEffect(() => {
		document.title = "About";
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, []);

	return (
		<div className="animate__animated  animate__fadeIn py-20">
			<PageTitle>About</PageTitle>
			<div className="sm:flex mb-16">
				<div className="relative h-80 w-full sm:w-56 flex-shrink-0 mb-5 sm:mb-0">
					<img
						src="/images/about/1.jpg"
						alt="me"
						className="relative z-10 h-80 w-full sm:w-56 object-cover"
					/>
					<div className="invisible sm:visible absolute left-4 top-4 border-4 h-80 w-56 border-primary" />
				</div>
				<div className="sm:pl-20">
					<h2 className="font-script text-5xl text-primary mb-3 font-normal">
						{`${name.firstName} ${name.surname}`}
					</h2>
					<p className="mb-3 text-gray-500">{about.shortDescription}</p>
					<p className="mb-9">{about.longDescription}</p>
					<Link
						to={ROUTES.CONTACT}
						className="bg-primary text-white px-8 py-4 uppercase tracking-wide text-sm">
						Contact Me
					</Link>
				</div>
			</div>
			<div className="mb-10">
				<H3>Skills</H3>
				<div className="sm:grid sm:grid-cols-2 sm:gap-x-16">
					{/* map through the skill array provided in the data file and display accordingly */}
					{skills.map((skill, index) => (
						<Skill
							key={index}
							title={skill.title}
							percentage={skill.percentage}
						/>
					))}
				</div>
			</div>
			<div>
				<H3>Reviews</H3>
				<div className="sm:grid sm:grid-cols-2 sm:gap-10 mb-5">
					{/* map through the review array provided in the data file and display accordingly,
                         displays a maximum of 4 reviews on page load, increments this number on click of the 'load more' button */}
					{reviews.slice(0, reviewCount).map((review, index) => (
						<Review key={index} index={index + 1} review={review} />
					))}
				</div>
				{reviews.length > reviewCount && (
					// button for display more reviews
					<button
						className="focus:outline-none mx-auto flex flex-col items-center justify-center space-y-1 p-5"
						onClick={() => setReviewCount(reviewCount + 4)}>
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-6 w-6 animate-bounce"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor">
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={2}
								d="M19 14l-7 7m0 0l-7-7m7 7V3"
							/>
						</svg>
						<span className="uppercase text-sm">Load more</span>
					</button>
				)}
			</div>
		</div>
	);
}
