// review component used to show a single review
export default function Reviews({ index, review }) {
	return (
		<div className="bg-gray-100 p-10 dark:bg-gray-800 mb-5 sm:mb-0 animate__animated animate__fadeIn">
			<img
				src={`/images/reviews/${index}.jpg`}
				alt="review"
				className="h-20 w-20 rounded-full object-cover mb-5"
			/>
			<p className="mb-3 text-gray-500 dark:text-gray-400">{review.review}</p>
			<p className="uppercase tracking-wide text-sm mb-0">
				<span className="text-gray-800 dark:text-white">- {review.name}</span> /{" "}
				{review.position} -
			</p>
		</div>
	);
}
